/* NavBar.css */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    color: #ffffff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
    transition: background-color 0.3s;
  }
  
  .light-mode .navbar {
    color: #000000;
  }
  
  .navbar-content {
    display: flex;
    gap: 20px;
  }
  
  .navbar-link {
    color: inherit; /* Use the inherited color to respect the light/dark mode */
    text-decoration: none;
    font-size: 16px;
  }
  
  .navbar-link:hover {
    text-decoration: underline;
  }
  