/* Dropdown.css */
.dropdown {
    display: flex;
    align-items: center;
  }
  
  .dropdown-toggle {
    font-size: 20px;
    cursor: pointer;
    color: #ffffff;
    margin-right: 40px;
  }

  .light-mode .dropdown-toggle {
    color: #000000;
  }
  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 30px;
    right: 0;
    background-color: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    overflow: hidden;
    transition: background-color 0.3s;
    margin-right: 40px;
    margin-top: 20px;
  }
  
  .dropdown-content div {
    padding: 10px;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .dropdown-content div:hover {
    background-color: #f0f0f0;
  }
  